import axios from 'axios';
import Navigation from 'components/modules/Navigation/Navigation';
import ArticleUnit from 'components/organisms/ArticleUnit/ArticleUnit';
import Foot from 'components/organisms/Footer/Foot';
import Header from 'components/organisms/Header/Header';
import { postFilter } from 'components/services/postFilter/postFilter';
import React, { useEffect, useState } from 'react';
import { useAppSelector } from 'state/hooks/hooks';
import { PostsWrapper, Wrap, Wrapper } from './MainPage.styles';

interface Props {}
export interface Post {
  title: string;
  description: string;
  id: number;
  tags: string;
}

const MainPage: React.FC<Props> = () => {
  const [posts, setIsPosts] = useState([
    { title: 'Tytuł', description: 'Lorem ipsum', tags: 'tag1,tag2', id: 0 },
    {
      title: 'Goodbye, Clean Code',
      description:
        'It was a late evening My colleague has just checked in the code that they’ve been writing all week. We were working on a graphics editor canvas, and they implemented the ability to resize shapes like rectangles and ovals by dragging small handles at their edges.The code worked.But it was repetitive. Each shape (such as a rectangle or an oval) had a different set of handles, and dragging each handle in different directions affected the shape’s position and size in a different way. If the user held Shift, we’d also need to preserve proportions while resizing. There was a bunch of math.',
      tags: 'tag1,tag2,tag3',
      id: 1,
    },
  ]);
  const isTitle = useAppSelector((state) => state.changeSearchState.title);
  const isDescription = useAppSelector(
    (state) => state.changeSearchState.description
  );
  const isTags = useAppSelector((state) => state.changeSearchState.tags);
  const searchValue = useAppSelector((state) => state.changeSearchState.value);
  useEffect(() => {
    let Local = localStorage.getItem('posts');
    if (Local !== null) {
      setIsPosts(JSON.parse(Local).reverse());
    }
  }, []);
  console.log(posts);

  return (
    <Wrap>
      <Wrapper>
        <Header />
        <Navigation />
        <PostsWrapper length={posts.length}>
          {posts.length !== 0
            ? //@ts-ignore
              posts.map((post: Post) => {
                if (searchValue.length !== 0) {
                  const filteredPost = postFilter(
                    isTitle,
                    isDescription,
                    isTags,
                    searchValue,
                    post
                  );
                  if (filteredPost) {
                    return (
                      <ArticleUnit
                        title={filteredPost.title}
                        text={filteredPost.description}
                        id={filteredPost.id}
                        key={filteredPost.id}
                        tags={filteredPost.tags}
                      />
                    );
                  }
                } else {
                  return (
                    <ArticleUnit
                      title={post.title}
                      text={post.description}
                      id={post.id}
                      key={post.id}
                      tags={post.tags}
                    />
                  );
                }
                return null;
              })
            : null}
        </PostsWrapper>
      </Wrapper>
      <Foot />
    </Wrap>
  );
};

export default MainPage;
